html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  font-family: "Roboto Condensed", sans-serif;
  letter-spacing: 0.03em;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*Start stylesheet*/
.body {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: auto;
  height: auto;
  background-color: darkgrey;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1440px;
  height: 960px;
  margin: auto;
  background-color: white;
}

.section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 1025px;
  height: 330px;
  margin-bottom: 534px;
}

.image {
  width: 550px;
  height: 330px;
  background-image: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_c_scale,w_1400.jpg");
  background-size: cover;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 380px;
  height: 250px;
}

.head-title {
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
}

.content-text {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
}

.button {
  color: #ffffff;
  background-color: #E4C1AE;
  width: 128px;
  height: 48px;
  border: none;
  border-radius: 25px;
  font-size: 16px;
  cursor: pointer;
  -webkit-border-radius: 25px;
  -moz-border-radius: 25px;
  -ms-border-radius: 25px;
  -o-border-radius: 25px;
}